import Vue from 'vue';
import VueRouter from 'vue-router';
import store from '../store'

import GameResult from '../pages/GameResult.vue'
import PointsSuccess from '../pages/PointsSuccess.vue'
import PointsFailed from '../pages/PointsFailed.vue'
import Game from '../pages/Game.vue'
import Notfound from "../pages/404.vue"
import {
  async
} from 'q';

const router = new VueRouter({
  routes: [{
      path: "/",
      name: "Game",
      component: Game
    },
    {
      path: "/gameresult",
      name: "GameResult",
      component: GameResult,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/pointssuccess",
      name: "PointsSuccess",
      component: PointsSuccess,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/pointsfailed",
      name: "PointsFailed",
      component: PointsFailed,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "*",
      component: Notfound
    }
  ],
  mode: "hash",
  base: process.env.BASE_URL
});

router.beforeEach(async (to, from, next) => {
  let history = store.state.anshistory.length;
  if (to.meta.requiresAuth) {
    //如果偵測到to.meta.requiresAuth，就進入驗證
    if (history == 0) {
      //若未回答則回到
      next({ path: "/"});
    } else {
      //若有回答
      // let isgetpoint = store.state.isGetPoint;
      // if(isgetpoint){
      //   next();
      // }
     
    }
  }
  next();
})

export default router;
