import Vue from 'vue'
import Vuex from 'vuex'
import {
  fail
} from 'assert';

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isShow: true,
    isShowAns: false,
    isShowNext: false,
    isCorrect: false,
    isGetPoint: false,
    islast: false,
    score: 0,
    anshistory: [],
    correct: 0,
    wrong: 0,
    allquiz: 0,
    isLoading: false,
    isTest: false
  },
  mutations: {
    startToPlay(state) {
      state.isShow = !state.isShow;
    },
    restartToPlay(state) {
      state.isShow = true;
      state.isShowAns = false;
      state.isShowNext = false;
      state.isCorrect = false;
      state.isGetPoint = false;
      state.islast = false;
      state.score = 0;
      state.anshistory = [];
      state.correct = 0;
      state.wrong = 0;
      state.allquiz = 0;
    },
    showAns(state) {
      state.isShowAns = !state.isShowAns;
    },
    getGrades(state,payload) {
      //結算成績
      state.islast = !state.islast;
    },
    getPoints(state,payload) {
      // 本日是否已兌換
      state.isGetPoint = payload;
    },
    addCorrect(state) {
      // 答對
      state.correct++;
      state.score = state.score + 20;

    },
    addWrong(state) {
      // 答錯
      state.wrong++;
    },
    pushAns(state, payload) {
      // 儲存回答紀錄
      state.anshistory.push(payload);
      state.allquiz = state.correct + state.wrong
    },
    mapMemberId(state, payload){
      //新增身分證屬性
      state.anshistory = state.anshistory.map(function(obj,index){
        obj.MemberId = payload;
        return obj;
      }) 
    },
    changeloading(state, change){
      let control = change == 'show'
      state.isLoading = control;
      let $body = document.getElementsByTagName('body')[0];
      if(control){
        $body.classList.add('modal-open');
      }else{
        $body.classList.remove('modal-open');
      }
    }
  },
  actions: {
    correctAdd({ commit}) {
      commit('addCorrect');
    },
    wrongAdd({commit}) {
      commit("addWrong");
    },
    ansPush({commit}, payload) {
      commit('pushAns', payload);
    },
    restart({commit}) {
      commit('restartToPlay');
    },
    pointsGet({commit},payload){
      commit('getPoints');
    },
    memberIdMap({commit}, payload){
      commit('mapMemberId', payload);
    },
    loading({commit}, payload){
      commit('changeloading', payload);
    }
  },
  getters: {
    getIsTest(state) {
      return state.isTest;
    },
    getIsmember(state) {
      return state.ismember;
    }
  }
})
