<template>
  <section id="anchor-part-2" class="activity section-padding">
      <!-- 裝飾 -->
      <img class="decoration_tree d-none d-md-block" src="../assets/images/decoration_tree.png" alt="decoration_tree.png">
      <img class="decoration_star d-none d-md-block" src="../assets/images/decoration_star.svg" alt="decoration_star.svg">
      <!-- 答題前 -->
      <div class="activity-infos  text-center" v-if="isShow"> 
        <h2 class="activity__title">保險知識大挑戰</h2>
        <p class="activity__text font24px m-0">天天拿走FinCash 60點不是夢！你準備好了嗎？</p>
        <p class="activity__text-notice mb-3">注意！沒有成為<a data-ga="奪Fin保險王-保險知識大挑戰-虛擬保險館" title="虛擬保險館" class="link font-weight-bold" href="https://vim.fubonlife.com.tw/" target="_blank">虛擬保險館</a>會員者，是拿不到點數的喔！</p>
        <a class="activity-main-btn mb-1" data-ga="奪Fin保險王-保險知識大挑戰-我準備好了" @click="getQuizData" title="我準備好了">我準備好了</a>
      </div>
       <!-- 答題中 -->
      <div class="activity-games" v-if="!isShow">
        <div class="activity-games__progressbar text-center">
          <ul class="bar">
            <li class="baritem" v-for="item in randomquiz.length" :key="item" :class="{'baritem-active': now + 1 == item}">
              <div class="first_icon" v-if="item == 1"></div>
              <div class="last_icon" v-else-if="item == randomquiz.length"></div>
              <div class="middle_icon" v-else></div>
            </li>
          </ul>
        </div>
        <div class="activity-games__question font-weight-bold d-flex align-items-start">  
          <div class="number font46px">{{quiz_num}}</div>
          <div class="text font24px">{{quiz_Txt}}</div>
        </div>
        <div class="activity-games__options text-center text-md-left">
          <div class="activity-games__options__pc-style">
            <div class="activity-games__options_btn" :class="{'activity-games__options_btn_disabled':isShowAns}">
              <div class="btnbox mb-3" v-for="(item, index) in random_opt" :key="index">
                <input :id="item.MultipleChoiceAnswerId" type="radio" :value="item.Answer" @click="chooseAns(item)"
                      :disabled="isShowAns" name="optionbtn" :key="`${item.QuestionId}_${item.MultipleChoiceAnswerId}`"/>
                <label :for="item.MultipleChoiceAnswerId" class="option"
                       :class="[isChooseAnsId == item.MultipleChoiceAnswerId ? status : '']" :checked="isChooseAnsId == item.MultipleChoiceAnswerId">
                       {{item.Answer}}
                </label>
              </div> 
            </div>
            <div class="answerbox d-md-flex align-items-md-start">
              <div class="answer" v-show="isShowAns">
                <span class="answer__title">答題解說</span>
                <span>{{quiz_comment}}</span>
              </div>
              <a class="activity-main-btn answer-btn" v-show="!isShowAns" title="看答案" @click="checkAns">看答案</a>
              <a class="activity-main-btn answer-btn" v-show="isShowAns && !islast" title="下一題" @click="next">下一題</a>
              <a class="activity-main-btn answer-btn" v-show="islast &&　isShowAns" title="結算成績" @click="getResult">結算成績</a>
            </div>
           </div>
        </div>
      </div>
      <!-- 尚未作答燈箱 -->
      <div>
        <b-modal id="bv-modal-example" header-class="modal_header" hide-footer>
          <template #modal-title>
          </template>
          <div class="d-block text-center">
            <p>您尚未作答喔！</p>
          </div>
          <div class="text-center">
          <a class="activity-main-btn restart_btn mb-1" title="繼續" 
              @click="$bvModal.hide('bv-modal-example')">繼續
            </a>
          </div>
        </b-modal>
      </div>
      <!-- api失敗燈箱 -->
      <div>
        <b-modal id="getapiFailed" header-class="modal_header" hide-footer>
          <template #modal-title>
          </template>
          <div class="d-block text-center">
            <p>發生異常...請重新整理後再試試</p>
          </div>
          <div class="text-center">
          </div>
        </b-modal>
      </div>
  </section>
</template>
<script>
import { mapState, mapMutations, mapActions, mapGetters } from "vuex";
import { CompatSource } from "webpack-sources";
import testData from "@json/quiz.js";
export default {
  name: "Game",
  props: {},
  components: {},
  data() {
    return {
      now: 0,
      isChooseAnsId: "",
      islast: false,
      isclicked: false,
      ifcorrect: false,
      questionId: "",
      status: "",
      anskey: "",
      comment: "",
      quizPayload: {
        subjectId: "School",
        questionType: "MultipleChoice",
        questionCount: 3
      },
      quizdata: []
    };
  },
  computed: {
    ...mapState([
      "isShow",
      "isShowAns",
      "isShowNext",
      "isCorrect",
      "isGetPoint",
      "correct",
      "wrong",
      "anshistory",
      "score",
      "allquiz"
    ]),
    ...mapGetters(["getIsTest"]),
    quiz_num() {
      let num = this.now + 1;
      return `Q${num}`;
    },
    quiz_Txt() {
      return this.randomquiz[this.now].Title || "";
    },
    quiz_opt() {
      return this.getNowQuiz().MultipleChoiceAnswerList || "";
    },
    randomquiz() {
      return this.randomArr(this.quizdata);
    },
    random_opt() {
      return this.randomArr(this.quiz_opt);
    },
    quiz_comment() {
      let comment = this.random_opt.filter((item, index) => {
        return item.IsCorrect == true;
      });
      return `(${this.anskey}) ${comment[0].Explanation}`;
    }
  },
  methods: {
    ...mapMutations([
      "startToPlay",
      "showAns",
      "getGrades"
    ]),
    ...mapActions(["loading"]),
    checkAns() {
      // 確認是否已選擇答案
      if (!this.isChooseAnsId) {
        this.$bvModal.show("bv-modal-example");
      } else {
        //公布答案
        this.showAns();
        //檢查回答
        if (this.ifcorrect) {
          this.status = "option-correct";
          this.$store.dispatch("correctAdd");
        } else {
          this.status = "option-wrong";
          this.$store.dispatch("wrongAdd");
        }
        //儲存回答紀錄
        this.$store.dispatch("ansPush", {
          AnswerId: this.isChooseAnsId,
          QuestionId: this.questionId,
          CreateTime: this.getCreateTime(),
          MemberId: "A119630384"
        });
      }
      this.anskey = this.convertToAlpha(this.getAnskey(this.random_opt));
    },
    next() {
      this.now++;
      this.showAns();
      //清空選項
      this.isChooseAnsId = "";
      this.status = "";
      if (this.now + 1 == this.quizdata.length) {
        this.islast = true;
      }
    },
    chooseAns(item) {
      this.isChooseAnsId = item.MultipleChoiceAnswerId;
      this.questionId = item.QuestionId;
      this.ifcorrect = item.IsCorrect;
      this.showAns;
    },
    getAnskey(arr) {
      let back = "";
      arr.forEach((item, index) => {
        if (item.IsCorrect == true) {
          back = index;
        }
      });
      return back;
    },
    convertToAlpha(num) {
      var code = 65 + num;
      return String.fromCharCode(code);
    },
    randomArr(arr) {
      if (arr != []) {
        return arr.sort(function() {
          //隨機打亂這個陣列
          return Math.random() - 0.5;
        });
      }
    },
    getResult() {
      this.getGrades();
      this.$router.push("GameResult");
    },
    getNowQuiz() {
      if (this.randomquiz[this.now]) {
        return this.randomquiz[this.now];
      }
    },
    getQuizData() {
      if (this.getIsTest) {
        this.quizdata = testData;
      } else {
        this.loading("show");
        const url = "/SchoolQuestionnaire/FindBySubjectId";
        let geturl ="";
        if(process.env.NODE_ENV === 'development'){
          geturl = "/life" + url;
        } else {
          geturl =  process.env.VUE_APP_LIFE_APIPATH + url;
        }
        var vm = this;
        vm.$.ajax({
          url: geturl,
          dataType: "json",
          async: true,
          data: {
            subjectId: "School",
            questionType: "MultipleChoice",
            questionCount: 3
          },
          type: "POST",
          success:function(response){
            //發送請求成功時
            // console.log(response)
            vm.quizdata = response;
            vm.startToPlay();
          },
          complete:function(){
            vm.loading("hide");
          },
          error:function(){
            //發送請求失敗時
            vm.$bvModal.show("getapiFailed");
          }
        });
        // this.$api({
        //   method: "POST",
        //   url: url,
        //   // baseURL: (process.env.NODE_ENV === 'development') ? '' : process.env.VUE_APP_LIFE_APIPATH,
        //   headers: {
        //     "Content-Type": "application/json;charset=UTF-8"
        //   },
        //   data: {
        //     subjectId: "School",
        //     questionType: "MultipleChoice",
        //     questionCount: 3
        //   }
        // })
        //   .then(response => {
        //     //發送請求成功時
        //     // console.log(response)
        //     this.quizdata = response.data;
        //     this.startToPlay();
        //   })
        //   .catch(error => {
        //     //發送請求失敗時
        //     this.$bvModal.show("getapiFailed");
        //   })
        //   .finally(() => {
        //     this.loading("hide");
        //   });
      }
    },
    getCreateTime() {
      var year = new Date().getFullYear(); //年
      if (year < 1900) year = year + 1900;
      var month = new Date().getMonth() + 1; //月
      if (month < 10) month = "0" + month;
      var day = new Date().getDate(); //日
      if (day < 10) day = "0" + day;
      var hour = new Date().getHours(); //小時
      if (hour < 10) hour = "0" + hour;
      var minute = new Date().getMinutes(); //分鐘
      if (minute < 10) minute = "0" + minute;
      var second = new Date().getSeconds(); //秒
      if (second < 10) second = "0" + second;
      var str =
        year +
        "/" +
        month +
        "/" +
        day +
        " " +
        hour +
        ":" +
        minute +
        ":" +
        second;
      return str;
    }
  }
};
</script>

<style scoped lang="scss">
::v-deep .modal-header {
  background-color: white;
  box-shadow: none;
}
::v-deep .modal-header:after {
  content: "";
  position: relative;
  top: 0;
  left: 0;
  height: auto;
  width: auto;
  font-size: 12px;
  border-right: 0 none;
  border-top: 0 none;
  border-left: 0 none;
  border-bottom: 0 none;
  z-index: 0;
}
::v-deep .modal-backdrop {
  opacity: 0.5;
}
.section-padding {
  padding-left: 15px;
  padding-right: 15px;
}
.link {
  color: $COLOR-LINK;
}
.activity {
  position: relative;
  min-height: calc(100vh - 600px);
  background-color: $BG-GAME;
  @include lg-media {
    min-height: calc(100vh - 672px);
  }

  .decoration_tree {
    position: absolute;
    width: 95px;
    height: 168px;
    bottom: -70px;
    z-index: 1;
    @include md-media {
      left: 3%;
    }
    @include lg-media {
      left: 8%;
    }
  }
  .decoration_star {
    position: absolute;
    right: 9%;
    bottom: -65px;
    z-index: 1;
  }
  &__title {
    color: $COLOR-TITLE;
    margin-bottom: 15px;
  }
  &-games {
    color: $COLOR-TITLE;
    &__question {
      position: relative;
      margin-bottom: 15px;
      width: 100%;
      text-align: left;
      @include md-media {
        margin: 0 auto 20px;
        width: 52%;
      }
      .number {
        // position: relative;
        // bottom: 5px;
        margin-right: 7px;
        line-height: 1;
        @include md-media {
          bottom: 10px;
          margin-right: 25px;
        }
      }
      .text {
        line-height: 1.3;
      }
    }
    &__options {
      width: 95%;
      margin: 0 auto;
      @include md-media {
        width: 52%;
      }
      .option {
        position: relative;
        color: $BTN-TEXT;
        background-color: $BTN-BG;
        border: 1px solid $BTN-TEXT;
        border-radius: 30px;
        padding: 10px 23px;
        font-weight: bold;
        margin: 0 20px;
        width: calc(100% - 40px);
        @include md-media {
          margin: 0;
          width: auto;
        }
        &-correct {
          background-color: $BTN-CORRECT !important;
        }
        &-wrong {
          background-color: $BTN-WRONG !important;
        }
      }
      .option::before {
        // margin-right: 1em;
        content: counter(listStyle, upper-alpha) " - ";
      }
      .option-correct:after {
        content: url("../assets/images/correct.svg");
        position: absolute;
        width: 27px;
        height: 27px;
        right: 0;
        margin-right: 0;
        padding-left: 10px;
        box-sizing: content-box;
        top: calc((100% - 27px) / 2);
        transform: translateX(100%);
      }
      .option-wrong:after {
        position: absolute;
        content: url("~@assets/images/wrong.svg");
        width: 27px;
        height: 27px;
        right: 0;
        margin-right: 0;
        padding-left: 10px;
        box-sizing: content-box;
        top: calc((100% - 27px) / 2);
        transform: translateX(100%);
      }
      input {
        display: none;
      }
      input:checked + label {
        color: white;
        background-color: $BTN-TEXT;
        border: 1px solid $BTN-ACTIVEBORDER;
      }
      &_btn {
        position: relative;
        counter-reset: listStyle;
        label {
          counter-increment: listStyle;
        }
        &:not(.activity-games__options_btn_disabled) {
          label {
            cursor: pointer;
            &:hover {
              color: white;
              background-color: lighten($BTN-TEXT, 15);
              border: 1px solid $BTN-ACTIVEBORDER;
            }
          }
        }
      }
      .answerbox {
        margin-top: 20px;
        margin-left: 20px;
        margin-right: 20px;
        @include md-media {
          margin-left: 0;
          margin-right: 0;
        }
      }
      .answer-btn {
        padding: 8px 20px;
        cursor: pointer;
        display: inline-block;
        word-break: keep-all;
        margin-top: 15px;
        @include md-media {
          margin-top: 0;
        }
      }
      .answer {
        border-radius: 20px;
        background-color: $BTN-BG;
        padding: 25px;
        text-align: center;
        min-width: 290px;
        width: 100%;
        margin-right: 15px;
        @include md-media {
          text-align: left;
          border-radius: 30px;
          // max-width: 624px;
          flex: 1;
        }
        &__title {
          font-weight: bold;
          display: block;
          margin-bottom: 5px;
          margin-right: 10px;
          @include md-media {
            display: inline;
            margin-bottom: 0;
          }
        }
      }
      &__pc-style {
        @include md-media {
          margin-left: 60px;
        }
        @include lg-media {
          margin-left: 85px;
        }
      }
    }
    &__progressbar {
      margin-top: 23px;
      ul li {
        display: inline-block;
      }
    }
    .bar {
      counter-reset: num;
    }
    .baritem {
      display: inline-block;
      position: relative;
    }
    .baritem:before {
      counter-increment: num;
      content: counter(num);
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      line-height: 1;
      top: 25%;
      color: white;
      opacity: 0.5;
    }
    .baritem-active:before {
      opacity: 1;
    }
    .baritem-active:after {
      content: url("../assets/images/boat_icon.png");
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      top: -25px;
    }
    .first_icon {
      background-image: url("../assets/images/first_icon.svg");
      background-size: cover;
      background-repeat: no-repeat;
    }
    .middle_icon {
      background-image: url("../assets/images/middle_icon.svg");
      background-size: cover;
      background-repeat: no-repeat;
    }
    .last_icon {
      background-image: url("../assets/images/last_icon.svg");
      background-size: cover;
      background-repeat: no-repeat;
    }
    .first_icon,
    .middle_icon,
    .last_icon {
      width: 43px;
      height: 26px;
    }
  }
}
</style>
