<template>
 <section id="anchor-part-3" class="activity section-padding">  
    <!-- 答題後 -->
    <div class="activity-result">
      <div class="container">
        <div class="d-flex flex-column flex-lg-row justify-content-center align-items-center">
          <div class="activity-result__grade">
            <div class="grade__infos-box">
              <img class="grade__infos-bg" src="../assets/images/grade_bg.svg" alt="您的成績">
            </div>
            <div class="grade__infos">
              <h3 class="grade__title font-weight-bold">您的成績</h3>
              <p class="grade__info font18px mb-0">總答題數：{{allquiz}}</p>
              <p class="grade__info font18px mb-0">正確題數：{{correct}}</p>
              <p class="grade__info font18px mb-3 font-weight-bold">可拿點數：{{score}}</p>
              <a class="activity-main-btn restart_btn mb-1" v-b-modal.restartConfirm title="重新測驗" 
              >重新測驗
              </a>
            </div>
          </div>
          <div class="activity-result__point text-lg-left text-center">
            <h3 class="point__title font-weight-bold">兌換點數</h3> 
            <div class="mb-3">
              <span class="point__txt font18px mt-1">請留下您在虛擬保險館的會員資料，以進行FinCash兌換：</span>
            </div>
            <div class="point__text-id d-flex flex-column flex-lg-row align-items-lg-center mx-auto">
              <span class="id text-left d-inline-block">身分證字號</span>
              <div class="d-inline-block flex1">
                <input class="w-100" type="text" v-model="memberId" placeholder="請輸入身分證字號">
              </div>
            </div>
            <div class="point__text-birth d-flex flex-column flex-lg-row align-items-lg-center mx-auto">
              <span class="birth text-left d-inline-block">生日</span>
              <div class="point__text-birth-box flex1 d-flex align-items-center">
                <!-- <v-select class="vueSelect mx-1 flex1" :options="years" v-model="year"/> -->
                <select class="custom-select w-auto mx-1 flex1" name="Year" v-model="year">
                <option v-for="item in years" :value="item" :key="item">{{item}}</option>
                </select>
                <div class="point__text-birth-txt">年</div>
                <!-- <v-select class="vueSelect mx-1 flex1" :options="months" v-model="month"/> -->
                <select class="custom-select w-auto mx-1 flex1" name="Month" v-model="month">
                  <option v-for="item in 12" :value="item" :key="item">{{item}}</option>
                </select>
                <div class="point__text-birth-txt">月</div>
                <!-- <v-select class="vueSelect mx-1 flex1" :options="days" v-model="day"/> -->
                <select class="custom-select w-auto mx-1 flex1" name="Day" v-model="day">
                  <option v-for="item in 31" :value="item" :key="item">{{item}}</option>
                </select>
              </div>
            </div>
            <p class="point__txt font18px mb-2">為確保您的權益，請妥善保管個人資料，切勿提供給任何人！</p>
          </div>
        </div>
        <div class="activity-result__notice">
          <div class="result__notice mb-3">注意！當日首次點選確定兌換後無法修改，請自行確認是否本次成績登入</div>
          <div class="result__notice-btn text-center">
            <a class="activity-main-btn mb-1" title="確定兌換" @click="submitGrades">確定兌換<i></i></a>
          </div> 
        </div>
      </div>
    </div>
    <!-- 尚未加入會員 -->
    <div>
      <b-modal id="isnotMember" header-class="modal_header" hide-footer>
        <template #modal-title>
        </template>
        <div class="d-block text-center">
          <p>請先加入<a class="font-weight-bold" href="https://vim.fubonlife.com.tw/" target="_blank">虛擬保險館</a>會員，才能兌換點數喔!</p>
        </div>
        <div class="text-center">
        <a class="activity-main-btn restart_btn mb-1" title="立即加入" @click="$bvModal.hide('isnotMember')" 
           target="_blank" href="https://vim.fubonlife.com.tw/">立即加入
          </a>
        </div>
      </b-modal>
    </div>
     <!-- api失敗燈箱 -->
    <div>
      <b-modal id="postgradesFailed" header-class="modal_header" hide-footer>
        <template #modal-title>
        </template>
        <div class="d-block text-center">
          <p>發生異常...請重新整理後再試試</p>
        </div>
        <div class="text-center">
        </div>
      </b-modal>
    </div>
    <!-- 身分證不正確 -->
    <div>
      <b-modal id="idError" header-class="modal_header" hide-footer>
        <template #modal-title>
        </template>
        <div class="d-block text-center">
          <p>請輸入正確的身分證格式</p>
        </div>
        <div class="text-center">
          <button class="activity-main-btn activity-button restart_btn mb-1" @click="$bvModal.hide('idError')">重新輸入</button>
        </div>
      </b-modal>
    </div>
    <!-- 重新測試 再次確認 -->
    <div>
      <b-modal id="restartConfirm" header-class="modal_header" hide-footer>
        <template #modal-title>
        </template>
        <div class="d-block text-center">
          <p>確認重新測驗</p>
        </div>
        <div class="text-center">
          <button class="activity-main-btn border-btn restart_btn" @click="$bvModal.hide('restartConfirm')">取消</button>
          <button class="activity-main-btn activity-button restart_btn ml-3" @click="playAgain">確認</button>
        </div>
      </b-modal>
    </div>
    <!-- 資料格式輸入錯誤(ValidateIdentity) -->
    <div>
      <b-modal id="TypeError" header-class="modal_header" hide-footer>
        <template #modal-title>
        </template>
        <div class="d-block text-center">
          <p>資料格式輸入錯誤</p>
        </div>
        <div class="text-center">
          <button class="activity-main-btn activity-button restart_btn mb-1" @click="$bvModal.hide('TypeError')">重新輸入</button>
        </div>
      </b-modal>
    </div>
  </section>

</template>
<script>
import { mapState, mapActions, mapGetters } from "vuex";
import { json } from 'body-parser';
export default {
  name: "GameResult",
  data() {
    return {
      memberId: "",
      year: "2021",
      month: "1",
      day: 1,
      birthday: new Date(),
      anshistoryData: "",
    };
  },
  computed: {
    ...mapState(["score", "allquiz", "correct", "anshistory"]),
    ...mapGetters(["getIsTest", "getIsmember"]),
    years() {
      const year = new Date().getFullYear();
      return Array.from(
        { length: year - 1900 },
        (value, index) => 1901 + index
      ).reverse();
    },
    months() {
      return Array.from({ length: 12 }, (value, index) => 1 + index);
    },
    days() {
      return Array.from({ length: 31 }, (value, index) => 1 + index);
    }
  },
  methods: {
    ...mapActions(["restart", "memberIdMap", "loading"]),
    playAgain() {
      this.restart();
      this.$router.push("/");
    },
    memberValidate(Id) {
      //驗證身分證格式
      let validate = /^[A-Za-z]{1}[1-2]{1}[0-9]{8}$/;
      if (validate.test(Id)) {
        return Id;
      } else {
        return false;
      }
    },
    async checkedMember(isValid) {
      let token = false;
      // 將生日轉為Date
      let month = this.month;
      if (month < 10) month = "0" + month;
      let day = this.day;
      if (day < 10) day = "0" + day;
      let birth = `${this.year}/${month}/${day}`;
      this.birthday = new Date(birth);
      var vm = this;
      // 身分證驗證結果
      if (isValid != false) {
        if (this.getIsTest) {
          alert("checkedMember!");
        } else {
          this.loading("show");
          //檢查是否為會員
          const memberUrl = '/Member/ValidateIdentity';
          let vimUrl = '';
          if(process.env.NODE_ENV === 'development'){
            vimUrl = "/vim" + memberUrl;
          }else{
            vimUrl = process.env.VUE_APP_VIM_APIPATH + memberUrl;
          }
          await vm.$.ajax({
            url: vimUrl, //請求的url地址
            dataType: "json", //返回格式為json
            async: true, //請求是否非同步，預設為非同步，這也是ajax重要特性
            data: { "Memberid": vm.memberId, "birthday": birth }, //引數值
            type: "POST", //請求方式
            success: function(response) {
              if (response.ResultCode == 1) {
                // 未註冊
                vm.$bvModal.show("isnotMember");
              } else {
                if (response.ResultCode == 2) {
                  //資料輸入錯誤
                  vm.$bvModal.show("TypeError");
                } else {
                  if (response.ResultCode == 0) {
                    //會員驗證成功
                    token = response.Token;
                  }
                }
              }
            },
            complete: function() {
              vm.loading("hide");
            },
            error: function() {
              vm.$bvModal.show("postgradesFailed");
            }
          });

          // await this.$api({
          //   method: "POST",
          //   url: vimUrl,
          //   // baseURL:  (process.env.NODE_ENV === 'development') ? '' : process.env.VUE_APP_VIM_APIPATH,
          //   data: { "Memberid": this.memberId, "birthday": birth },
          //   headers: {
          //     "Accept": "application/json, text/javascript",
          //     "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8",
          //   //   "Access-Control-Allow-Origin": "*",
          //   //   "Access-Control-Request-Headers": "X-PINGOTHER, Content-Type",
          //   //   "crossorigin": true
          //   },
          //   // withCredentials: false,
            
          // })
          // .then(response => {
          //   //發送請求成功時若為會員則送出成績
          //   // console.log('驗證會員api成功');
          //   if (response.data.ResultCode == 1) {
          //     // 未註冊
          //     this.$bvModal.show("isnotMember");
          //   } else {
          //     if (response.data.ResultCode == 2) {
          //       //資料輸入錯誤
          //       this.$bvModal.show("TypeError");
          //     } else {
          //       if (response.data.ResultCode == 0) {
          //         //會員驗證成功
          //         token = response.data.Token;
          //       }
          //     }
          //   }
          // })
          // .catch(error => {
          //   //發送請求失敗時
          //   // console.log("驗證會員失敗");
          //   this.$bvModal.show("postgradesFailed");
          // })
          // .finally(() => {
          //   this.loading("hide");
          // });
        }
      } else {
        // 身分證格式報錯
        vm.$bvModal.show("idError");
      }
      return token;
    },
    submitGrades() {
      if (this.getIsTest) {
        alert("submitGrades");
      } else {
        //會員驗證
        let $token = this.checkedMember(this.memberValidate(this.memberId)).then((token)=>{
          if(token){
            //加入身分證後轉為字串
            this.memberIdMap(this.memberId);
            let sendData = {
              "token": token,
              "questionnaireResultList": this.$store.state.anshistory
            };
            this.loading("show");
            // console.log(sendData)
            const replyUrl = "/SchoolQuestionnaire/Reply";
            let lifeUrl = '';
            if(process.env.NODE_ENV === 'development'){
              lifeUrl = "/life" + replyUrl;
            }else{
              lifeUrl = process.env.VUE_APP_LIFE_APIPATH + replyUrl;
            }
            var vm = this;
            vm.$.ajax({
              url: lifeUrl,
              // headers: {
              //   // "Access-Control-Allow-Origin": "*",
              //   "Token":token
              // },
              // async: true, //請求是否非同步，預設為非同步，這也是ajax重要特性
              data: sendData,
               contentType: 'application/x-www-form-urlencoded',
               type:"POST",
               success:function(response){
                //  console.log('success:'+typeof(response))
                if (response == "True") {
                  //本日尚未兌換
                  vm.$router.push("PointsSuccess");
                  vm.$store.dispatch("pointsGet", true);
                } else {
                  if(response == "False"){//本日已經兌換
                  vm.$router.push("PointsFailed");
                  vm.$store.dispatch("pointsGet", false);}
                }
               },
               complete:function(data){
                  vm.loading("hide");
               },
               error: function(reje) {
                //  console.log('error:'+reje);
                //發送請求失敗時
                // console.log("送出成績失敗");
                vm.$bvModal.show("postgradesFailed");
               }
            });
            // this.$api({
            //   method: "POST",
            //   url: lifeUrl,
            //   // baseURL:  (process.env.NODE_ENV !== 'development') ? '' : process.env.VUE_APP_LIFE_APIPATH,
            //   headers: {
            //     "Content-Type": "application/json;charset=UTF-8",
            //     "Access-Control-Allow-Origin": "*",
            //     "Token":token
            //   },
            //   // data: { "questionnaireResultList":[{ "MemberId" : "A119030384", "QuestionId" : "School-1", "AnswerId":"1", "CreateTime" : "2020/12/29 19:00:01" }, { "MemberId" : "A119030384", "QuestionId" : "School-2", "AnswerId":"2", "CreateTime" : "2020/12/29 19:00:01" }, { "MemberId" : "A119030384", "QuestionId" : "School-3", "AnswerId":"1", "CreateTime" : "2020/12/29 19:00:01" } ] },
            //   data: JSON.stringify(sendData)
            // })
            //   .then(response => {
            //     // console.log('submitGrades成功')
            //     if (response.data == "True") {
            //       //本日尚未兌換
            //       this.$router.push("PointsSuccess");
            //       this.$store.dispatch("pointsGet", true);
            //     } else {
            //       //本日已經兌換
            //       this.$router.push("PointsFailed");
            //       this.$store.dispatch("pointsGet", false);
            //     }
            //   })
            //   .catch(error => {
            //     //發送請求失敗時
            //     // console.log("送出成績失敗");
            //     this.$bvModal.show("postgradesFailed");
            //   })
            //   .finally(() => {
            //     this.loading("hide");
            //   });
          }
        });
      }
    },
    objTostring(obj) {
      this.anshistoryData = JSON.stringify(obj);
    }
  }
};
</script>

<style lang="scss" scoped>
::v-deep .modal-header {
  background-color: white;
  box-shadow: none;
}
::v-deep .modal-header:after {
  content: "";
  position: relative;
  top: 0;
  left: 0;
  height: auto;
  width: auto;
  font-size: 12px;
  border-right: 0 none;
  border-top: 0 none;
  border-left: 0 none;
  border-bottom: 0 none;
  z-index: 0;
}
::v-deep .modal-backdrop {
  opacity: 0.5;
}
.activity {
  &-result {
    background-color: $BG-GAME;
    padding-top: 30px;
    &__grade {
      text-align: center;
      position: relative;
      @include md-media {
        padding: 0 15px;
      }
      .grade__infos-box {
        width: 286px;
        height: 246px;
      }
      .grade__infos-bg {
        position: relative;
      }
      .grade__infos {
        text-align: center;
        position: absolute;
        top: 54%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
      .grade__title {
        color: $COLOR-TITLE;
        margin-bottom: 15px;
      }
      .grade__info {
        margin-bottom: 8px;
      }
      .restart_btn {
        color: $COLOR-LINK;
        border: 1px solid $COLOR-LINK;
        background-color: rgb(255, 255, 255);
      }
      .restart_btn:hover {
        background-color: $BTN-OUTLINE-HOVER;
      }
    }
    &__point {
      margin-top: 40px;
      @include lg-media {
        margin-top: 0;
        padding: 0 15px;
      }
      .point__title {
        color: $COLOR-TITLE;
      }
      .point__txt {
        color: $COLOR-SUBTITLE;
      }
    }
    .id,
    .birth {
      min-width: 5em;
      margin-right: 15px;
    }
    .point__text-id,
    .point__text-birth {
      max-width: 500px;
      margin-bottom: 15px;
      @include lg-media {
        max-width: auto;
      }
    }
    .point__text-birth-box {
      margin-left: -0.25rem;
      margin-right: -0.25rem;
    }
    .point__text-id {
      input {
        border: 1px solid #ced4da;
        border-radius: 4px;
        padding: 6px;
        &:focus {
          border-color: #80bdff;
          outline: 0;
          box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
        }
        &::placeholder {
          color: #ccc;
        }
      }
    }
    .point__text-birth-txt {
      margin-left: 5px;
      margin-right: 5px;
    }
    .flex1 {
      flex: 1;
    }
    &__notice {
      text-align: center;
      margin-top: 0;
      @include md-media {
        margin-top: 40px;
      }
    }
  }
  &-button {
    border: 0;
  }
}
.border-btn {
  color: $COLOR-LINK;
  border: 1px solid $COLOR-LINK;
  background-color: rgb(255, 255, 255);
  &:hover {
    background-color: $BTN-OUTLINE-HOVER;
  }
}
.vueSelect {
  background: #fff;
}
</style>
